$mainPadding: 6px 16px;
$mainColor: #ff564c;

$aColor: #6f6e6e;
$TitleColor: #3a3635;

$smallFont: 12px;
$contTitleSize: 18px;
$contTitlePadding: 0 28px 0 0;

$mainBorder: 1px solid #ff564c;
$mainBorder2: 2px solid #ff564c;
$titleBottomLine: 2px solid #4c4c4c;

body {
    min-width: 1100px;
}

.contTitle {
    font-size: $contTitleSize;
    padding: $contTitlePadding;
}

.appHeader {
    position: sticky;
    top: 0;
    width: 100%;
    background: #fff;
    border-bottom: 1px solid #d9d9d9;
    .hdTnbWrap {
        display: flex;
        justify-content: space-between;
        padding: $mainPadding;
        .hdTnb {
            display: flex;
            a {
                color: $aColor;
                font-size: $smallFont;
                &:hover {
                    text-decoration: underline;
                }
            }
        }
        .hdTnb1 {
            & > li {
                padding-right: 8px;
                &:last-child {
                    padding-right: 0;
                }
            }
        }
        .hdTnb2 {
            & > li {
                padding-left: 8px;
                &:first-child {
                    padding-left: 0;
                }
            }
        }
    }
    .hdWrap {
        display: flex;
        justify-content: space-between;
        padding: $mainPadding;
        .hdWrap1 {
            display: flex;
            .searchBar {
                border: $mainBorder2;
                width: 374px;
                height: 40px;
                border-radius: 100px;
                box-sizing: border-box;
                padding: 14px 18px;
                margin: 20px 50px 20px 140px;
            }
        }
        .hdWrap2 {
            line-height: 84px;
            font-size: $smallFont;
            a {
                color: $aColor;
                padding-right: 12px;
                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
}

.appContant {
    display: flex;
    & > nav {
        position: sticky;
        top: 0;
        min-width: 220px;
        padding: 12px 16px;
        & > div {
            border-bottom: 1px solid #efefef;
            padding-bottom: 16px;
            margin-bottom: 16px;
            text-align: center;
            & > a {
                display: block;
                width: 100%;
                border: 1px solid #d9d9d9;
                font-size: $smallFont;
                color: $TitleColor;
                padding: 10px 0;
                margin-bottom: 12px;
            }
            & > div {
                display: flex;
                justify-content: space-between;
                font-size: $smallFont;
                a {
                    color: $TitleColor;
                    display: block;
                    width: 50%;
                    &:nth-child(1) {
                        border-right: 1px solid #ccc;
                    }
                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }
    }
    .mainContant {
        width: 87%;
        .latestMusic {
            background: #dfe5e8;
            box-sizing: border-box;
            padding: 32px 40px;
            .latestMusic1 {
                display: flex;
                justify-content: space-between;
                border-bottom: $titleBottomLine;
                padding-bottom: 20px;
                margin-bottom: 10px;
                .latestMusic1_1 {
                    display: flex;
                }
            }
        }
        .mainSideAd {
            display: flex;
            .adSlide {
                width: 50%;
            }
            a {
                display: block;
            }
        }
    }
}

.slick-slide {
    img {
        margin: 0 auto;
    }
}
.slick-dots {
    bottom: 0 !important;
    right: 0 !important;
    display: inline-block;
}
